@import 'assets/scss/variables.scss';

.wrapper {
    color: $code;
    font-size: $textPrimary;
    font-family: $secondaryFont;
    font-weight: 300;
    & span {
        color: $light;
        padding-left: 2rem;
        display: inline-block;
    }
    & .h-tag {
        &~span {
            font-size: $textLarge;
            font-weight: 600;
        }   
    }
}